var Vue = require('vue/dist/vue.js')
var VueRouter = require('vue-router/dist/vue-router.common.js')
require('bootstrap-css-only')
require('../css/main.sass')

var Menu = {
	' 1': '/',
	' 2': '/task2',
	' 3': '/task3',
	' 4': '/task4'
}

// 
var Task1 = require('../pages/Task1.vue').default //  1: "C  "
var Task2 = require('../pages/Task2.vue').default //  2: ""
var Task3 = require('../pages/Task3.vue').default //  3: " "
var Task4 = require('../pages/Task4.vue').default //  4: "PUSH-"
var NotFound = require('../pages/404.vue').default

Vue.use(VueRouter)

var router = new VueRouter({
	mode: 'history',
	base: '/frontend-demo/',
	routes: [
	{path: '/', component: Task1},
	{path: '/task2', component: Task2},
	{path: '/task3', component: Task3},
	{path: '/task4', component: Task4},
	{path: '*', component: NotFound}
	]
})

var app = new Vue({
	el: '#app',
	router: router,
	data: {
		menu: Menu
	}
});



